<template>
    <div class="main">
        <Bar title="站点统计" message="用户、文章、评论的统计信息"></Bar>
        <el-row :gutter="20">
            <el-col :span="8">
                <el-card class="box-card">
                    <div>用户数量</div>
                    <div class="count">{{ user }}</div>
                </el-card>
            </el-col>
            <el-col :span="8">
                <el-card class="box-card">
                    <div>文章数量</div>
                    <div class="count">{{ article }}</div>
                </el-card>
            </el-col>
            <el-col :span="8">
                <el-card class="box-card">
                    <div>评论数量</div>
                    <div class="count">{{ comment }}</div>
                </el-card>
            </el-col>
        </el-row>
        <Bar title="常用设置" message="设置网站的名称"></Bar>
        <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="站点名称">
                <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">保存</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import Bar from './Bar.vue'

export default {
    name: 'Main',
    data () {
        return {
            form: {name: 'treesay'},
        }
    },
    computed: {
        user: function () { return this.$store.counts.user; },
        article: function () { return this.$store.counts.article; },
        comment: function () { return this.$store.counts.comment; },
    },
    components: { Bar },
    methods: {
        onSubmit () {},
    },
}
</script>

<style scoped>
    .el-form { width: 50%; }
    .count {
        font-size: 24px;
        color: #58B7FF;
    }
</style>




